.advantages__title {
  margin: 0;
  margin-bottom: 40px;
  font-family: $font-oswald;
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 40px;
  color: $basic_black;

  @media (min-width: $tablet-width) {
    margin-bottom: 72px;
    font-size: 60px;
    line-height: 60px;
  }
}

.advantages__list {
  @include list-reset;

  display: grid;
  grid-template-columns: 1fr;
  row-gap: 20px;

  @media (min-width: $tablet-width) {
    grid-template-columns: 1fr 1fr;
    column-gap: 152px;
    row-gap: 90px;
  }

  @media (min-width: $desktop-width) {
    grid-template-columns: repeat(4, 1fr);
    column-gap: 94px;
    row-gap: 0;
  }
}

.advantages__item {
  position: relative;
  display: flex;
  align-items: center;

  @media (min-width: $tablet-width) {
    min-height: 226px;
    flex-direction: column;
    align-items: start;
  }
}

@media (min-width: $tablet-width), (min-width: $desktop-width) {
  .advantages__item--nutrients {
    &::after {
      content: "1";
      position: absolute;
      top: -38px; //Подгон
      right: 0;
      display: block;
      width: 120px;
      height: 280px;
      color: $basic_grey;
      font-family: "Oswald", sans-serif;
      font-size: 280px;
      line-height: 280px;
      z-index: -1;
    }
  }

  .advantages__item--powder {
    &::after {
      content: "2";
      position: absolute;
      top: -38px; //Подгон
      right: 0;
      display: block;
      width: 120px;
      height: 280px;
      color: $basic_grey;
      font-family: "Oswald", sans-serif;
      font-size: 280px;
      line-height: 280px;
      z-index: -1;
    }
  }

  .advantages__item--meal {
    &::after {
      content: "3";
      position: absolute;
      top: -38px; //Подгон
      right: 0;
      display: block;
      width: 120px;
      height: 280px;
      color: $basic_grey;
      font-family: "Oswald", sans-serif;
      font-size: 280px;
      line-height: 280px;
      z-index: -1;
    }
  }

  .advantages__item--changes {
    &::after {
      content: "4";
      position: absolute;
      top: -38px; //Подгон
      right: 0;
      display: block;
      width: 120px;
      height: 280px;
      color: $basic_grey;
      font-family: "Oswald", sans-serif;
      font-size: 280px;
      line-height: 280px;
      z-index: -1;
    }
  }
}

.advantages__item--nutrients::before {
  @include advantages-icon;
  background-image: url("../img/advantages/nutrients.svg");
}

.advantages__item--powder::before {
  @include advantages-icon;
  background-image: url("../img/advantages/powder.svg");
}

.advantages__item--meal::before {
  @include advantages-icon;
  background-image: url("../img/advantages/meal.svg");
}

.advantages__item--changes::before {
  @include advantages-icon;
  background-image: url("../img/advantages/changes.svg");
}

.advantages__text {
  margin: 0;

  @media (min-width: $tablet-width) {
    max-width: 220px;
  }

  @media (min-width: $desktop-width) {
    font-size: 16px;
    line-height: 18px;
  }
}
